.n {
    position: fixed;
    z-index: 9999;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
}

.n-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 20px 60px;
}

.n-left {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border-radius: 50%;
    border: 4px solid #fff;
}


.wrapper-n {
    width: 100%;
    position: fixed;
    top: 0;
    font-family: montserrat, sans-serif;
    background-color: rgb(13, 27, 48);
    
   
}
.n-right {
    
    padding-right: 115px;
}

.n-right ul {
    display: flex;
    list-style: none;
    color: white;
}

.n-right ul li a {
    margin: 0 15px;
    cursor: pointer;
    transition: color 0.8s ease-in-out;
}

.active {
    color: #4070f4;
}

.n-right ul li a:hover {
    color: #4070f4;
}

.mobile-menu {
    display: none;
}

@media only screen and (max-width: 500px) {
    .n-wrapper {
        padding: 20px 30px;
    }
    .mobile-menu .menu-icon {
        cursor: pointer;
    }
    .n-right ul {
        display: none;
    }
    .mobile-menu {
        display: block;
    }
    .menu-list {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        height: 100vh;
    }
    .menu-list ul {
        display: flex;
        flex-direction: column;
        padding: 40px;
        background-color: #000;
    }
    .menu-list ul li {
        margin: 20px 0;
    }
    .close {
        position: absolute;
        top: 20px;
        right: 20px;
    }
}